.intro__frame {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: $PRIMARYCOLOR;
    width: 100%;
    height: 100%; }

.intro__introduction {
    display: flex;
    flex-direction: row; }

.intro__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    padding: 24px;
    margin: $MARGIN16;
    border-radius: 50%;
    width: 250px;
    height: 250px; }

.intro__item:hover {
    background-color: $LIGHTCOLOR; }

.intro__span {
    font-family: $FONTROBOTOCONDENSED;
    font-size: 20px;
    font-style: oblique;
    font-weight: 300;
    color: $BLACKTEXTSECONDARY;
    margin: $MARGIN32;
    text-align: center;
    vertical-align: text-top; }

.intro__button__block {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    font-family: $FONTROBOTOCONDENSED;
    font-weight: bold;
    margin-top: 96px; }

.intro__button__settings {
    display: inline-block;
    box-sizing: border-box;
    position: relative;
    font: inherit;
    background: $SECONDARYCOLOR;
    box-shadow: $SHADOW1;
    color: $WHITETEXTPRIMARY;
    cursor: pointer;
    padding: 12px 16px;
    margin: $MARGIN16;
    border: none;
    border-radius: 3px;
    letter-spacing: 0.3px;
    text-align: center;
    min-width: 5.14em;
    text-transform: uppercase;
    user-select: none; }

.intro__button__settings:hover {
    box-shadow: $SHADOW2; }

.intro__button__uploadfile {
    position: relative; }

.intro__button__loadfile {
    display: inline-block;
    box-sizing: border-box;
    position: relative;
    font: inherit;
    background: $SECONDARYCOLOR;
    box-shadow: $SHADOW1;
    color: $WHITETEXTPRIMARY;
    padding: 9px 16px;
    margin: $MARGIN16;
    border-radius: 3px;
    letter-spacing: 0.3px;
    text-align: center;
    min-width: 5.14em;
    text-transform: uppercase;
    user-select: none; }

.intro__button__next {
    position: absolute;
    top: 18px;
    right: 16px;
    padding: 8px; }
